<template>
    <div>
        <Loading :active="isLoading"></Loading>
        <div class="text-end mt-4">
            <button class="btn btn-primary" @click.passive="openCouponModel()">
                建立新的優惠券
            </button>
        </div>
        <table class="table mt-4">
            <thead>
                <tr>
                    <th>名稱</th>
                    <th>折扣百分比</th>
                    <th>到期日</th>
                    <th>是否啟用</th>
                    <th>編輯</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, key) in this.CouponData" :key="key">
                    <td>{{ item.title }}</td>
                    <td>{{ item.percent }}%</td>
                    <td>{{ $filters.date(item.due_date) }}</td>
                    <td>
                        <span v-if="item.is_enabled === 1" class="text-success">啟用</span>
                        <span v-else class="text-muted">未起用</span>
                    </td>
                    <td>
                        <div class="btn-group">
                            <button class="btn btn-outline-primary btn-sm"
                                @click="openCouponModal(false, item)">編輯</button>
                            <button class="btn btn-outline-danger btn-sm" @click="openDelCouponModal(item)">刪除</button>
                        </div>
                    </td>
                </tr>
            </tbody>
        </table>
        <couponModal ref="couponModal" @update-coupon="updateCoupon" />
        <DelModal :item="tempCoupon" ref="delModal" @del-item="delCoupon" />
        <Loading :active="isLoading"></Loading>
    </div>
</template>
<script>
import CouponModal from '../components/CouponModal.vue';
// import DelModal from '../components/DelModal.vue';S
export default {
    components: { CouponModal },
    props: {
        config: Object
    },
    data () {
        return {
            tempCoupon: {},
            isLoading: false,
            CouponData: []
        };
    },
    methods: {
        getCouponData () {
            this.isLoading = true;
            const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/coupons`;
                this.$http.get(api)
                .then((res) => {
                    this.isLoading = false;
                    if (res.data.success) {
                        this.CouponData = res.data.coupons;
                        this.isLoading = false;
                    }
                });
        },
        openCouponModel () {
            const productComponent = this.$refs.couponModal;
            productComponent.showModel();
        },
        updateCoupon (item) {
                this.isLoading = true;
                this.tempCoupon = item;
                const dateTime = +new Date(this.tempCoupon.due_date);
                const timestamp = Math.floor(dateTime / 1000);
                this.tempCoupon.due_date = timestamp;
                console.log(timestamp);
                const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/coupon`;
                this.$http.post(api, { data: this.tempCoupon })
                .then((response) => {
                    this.getCouponData();
                    this.isLoading = false;
                });
            const productComponent = this.$refs.couponModal;
            productComponent.hideModel();
            console.log(item);
        }
    },
    created () {
        this.getCouponData();
    }
};
</script>
